#home {
  height: calc(100vh - 5rem);
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  text-shadow: 10px 5px 5px rgba(0, 0, 0, 0.5);
}

.profilePhoto {
  position: absolute;
  top: 2rem;
  height: 80vh;
  object-fit: cover;
  z-index: -1;
  right: 0;
  border-radius: 50px;
}

.homeContent {
  height: 80vh;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.getResumeBtn {
  border: none;
  padding: 1rem;
  border-radius: 5rem;
  font-size: 18px;
  background-color: rgb(53, 53, 53);
  color: white;
  border-radius: 10px;
  width: 9rem;
  margin-top: 1rem;
}
.getResumeBtn:hover {
  background-color: rgb(214, 214, 214);
  color: rgb(53, 53, 53);
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.351);
}
.hello {
  font-size: 25px;
  font-weight: 400;
}
.intro {
  font-size: 50px;
  font-weight: 700;
  letter-spacing: 1px;
}

.name {
  color: rgb(137, 78, 78);
}
.introDesc {
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 1px;
}

@media screen and (max-width: 860px) {
  #home {
    width: 95%;
  }
  .hello {
    font-size: 20px;
    font-weight: 400;
  }
  .intro {
    font-size: 40px;
    font-weight: 700;
  }

  .introDesc {
    font-size: 14px;
    font-weight: 300;
  }
  .getResumeBtn {
    font-size: 14px;
    width: 7rem;
  }
}

@media screen and (max-width: 570px) {
  .profilePhoto {
    height: 60vh;
    top: 2rem;
  }
  .homeContent{
    height: 60vh;

  }
  #home{
    height: calc(80vh - 5rem);
  }
}
