#projects {
  width: 80%;
  margin: 0 auto;
  margin-top: 3rem;
}
.projectsList {
  width: 90%;
  margin: 0 auto;
}
.projectsBlock {
  display: flex;
  align-items: center;
  margin: 2rem 0;
  border: 1px solid black;
  background-color: rgb(57, 57, 57);
  border-radius: 10px;
}
.projectTitle {
  margin-bottom: 10px;
}
.projectDesc {
  margin-bottom: 10px;
}
.projectsBlock > img {
  width: 30rem;
  border-radius: 10px;
  margin-right: 2rem;
}

.projectbtn {
  font-size: 15px;
  margin: 5px;
  height: 35px;
  width: 80px;
  border-radius: 5px;
  border: none;
  background-color: white;
}
.projectbtn:hover {
  transform: scale(1.1);
  transition: 0.3s;
  box-shadow: rgba(255, 255, 255, 0.2) 0px 7px 29px 0px;
}

@media screen and (max-width: 1300px) {
  .projectsBlock > img {
    width: 25rem;
  }
}
@media screen and (max-width: 1100px) {
  .projectsBlock > img {
    width: 20rem;
  }
  .projectTitle {
    font-size: 17px;
  }
  .projectDesc {
    font-size: 14px;
  }
  .projectbtn {
    font-size: 15px;
    height: 25px;
    width: 70px;
  }
}
@media screen and (max-width: 860px) {
  #projects {
    width: 98%;
  }
  .projectsBlock > img {
    width: 15rem;
  }
  .projectTitle {
    font-size: 14px;
  }
  .projectDesc {
    font-size: 10px;
  }
  .projectbtn {
    font-size: 13px;
    height: 22px;
    width: 60px;
  }
}
@media screen and (max-width: 570px) {
  .projectsBlock {
    flex-direction: column;
    text-align: center;
  }
  .projectsBlock > img {
    margin: 0 auto;
  }
  .projectTitle {
    margin-top: 5px;
  }
  .projectDesc {
    margin-top: 5px;
  }
}
