.navbar {
  height: 5rem;
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 15px;
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: rgb(30, 30, 30);
}

.logo {
  height: 5rem;
  width: 5rem;
  object-fit: cover;
  font-family: "Black Ops One", sans-serif;
  font-size: 70px;
  cursor: pointer;
}
.logo:hover {
  text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.351);
}

.desktopMenuList {
  margin: 1rem;
  cursor: pointer;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 500;
  transform: translateY(0px);
  transition: transform 0.2s ease-in-out;
}

.desktopMenuList:hover {
  transform: translateY(-3px);
  color: rgb(214, 214, 214);
  text-shadow: 3px 3px 5px rgba(169, 167, 167, 0.457);
  border-bottom: 2px solid rgb(199, 199, 199);
  padding-bottom: 8px;
}

.desktoMenuBtn {
  border: none;
  padding: 1rem;
  border-radius: 5rem;
  font-size: 18px;
  background-color: rgb(53, 53, 53);
  color: white;
  border-radius: 10px;
}
.desktoMenuBtn:hover {
  background-color: rgb(214, 214, 214);
  color: rgb(53, 53, 53);
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.351);
}

.burgerMenuImg{
  display: none;
  height: 3rem; 
}

.burgerMenu{
  position: absolute;
  top: 5rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 5px;
  height: fit-content;
  border-radius: 10px;
  width: 12rem;
  backdrop-filter: blur(10px)

}
.burgerListItem{
  padding: 7px;
  text-align: center;
  background-color: black;
  margin: 7px;
  border-radius: 5px;
}

@media screen and (max-width: 860px) {
  .navbar {
    width: 95%;
  }
  .desktopMenuList {
    font-size: 14px;
    margin: 10px;
  }
  .desktoMenuBtn {
    font-size: 14px;
  }
  .logo {
    height: 3rem;
    width: 3rem;
  }
}

@media screen and (max-width: 570px){
  .logo{
    font-size: 40px;
  }
  .burgerMenuImg{
    display: block;
    height: 2rem;
  }
  .desktopMenuList{
    display: none;
  }
  .desktoMenuBtn{
    display: none;
  }
}
