#contact {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.contactTitle {
  margin-top: 50px;
  font-size: 35px;
}
.contactDesc {
  font-size: 20px;
}

.contactForm {
  display: flex;
  flex-direction: column;
  width: 40rem;
  margin: auto;
  margin-top: 30px;
}

.contactForm * {
  margin-top: 15px;
  font-size: 15px;
  padding: 7px;
  border-radius: 7px;
  color: white;
  background-color: rgb(81, 81, 81);
  border: none;
}
.contactForm *::placeholder {
  color: white;
}
.contactForm > input {
  height: 30px;
}

.submitBtn {
  width: 80px;
  margin: auto;
  margin-top: 20px;
  cursor: pointer;
  font-size: 15px;
  color: black;
  background-color: white;
}

.socialLink {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-top: 20px;
}

.socialLink > a > img {
  height: 30px;
  width: 30px;
  margin: 10px;
  cursor: pointer;
}
@media screen and (max-width: 860px) {
  .contactTitle {
    font-size: 25px;
    margin-top: 20px;
  }
  .contactDesc {
    font-size: 15px;
  }
  .contactForm {
    width: 30rem;
    margin-top: 20px;
  }
}
@media screen and (max-width: 570px) {
  #contact {
    width: 95%;
  }
  .contactTitle {
    font-size: 20px;
  }
  .contactDesc {
    font-size: 12px;
  }
  .contactForm {
    width: 18rem;
    margin-top: 20px;
  }
  .contactForm * {
    font-size: 12px;
  }
}
