#skills {
  width: 80%;
  margin: 0 auto;
  margin-top: 3rem;
}
.skillsList{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 2rem;

}
.skillsBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  background-color: white;
  color: black;
  width: 8rem;
  padding: 10px;
  border-radius: 10px;
}
.skillsBlock:hover {
  transform: scale(1.1);
  transition: 0.3s;
  box-shadow: rgba(255, 255, 255, 0.4) 0px 7px 29px 0px;}

.skillsBlock > img {
  width: 6rem;
  margin-bottom: 1rem;
}
