#about{
    width: 80%;
    margin: 0 auto;
}

.aboutTitle{
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 20px;
}
.aboutDesc{
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 15px;
    width: 80%;
    margin: auto;
    text-align: center;
    letter-spacing: 0.5px;
}

@media screen and (max-width: 860px) {
    #about{
        width: 95%;
    }
    .aboutTitle{
        font-size: 40px;
    }
    .aboutDesc{
        font-size: 13px;
        width: 90%;
    }
}

